.btnAdd {
  display: inline-block;
  box-sizing: border-box;
  border: 2px solid #362944;
  margin: 2px;
  padding: 0px;
  width: 20px;
  height: 20px;
  overflow: visible;

  background-color: #f0f6e8;

  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  line-height: 0px;
}
